.baby-mode {
  display: flex;
  flex-direction: column;
  height: 100%;
  background: #000;
}

.baby-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 12px 16px;
  z-index: 1;
}

.baby-pin {
  font-size: 14px;
  color: var(--text-secondary);
  background: var(--bg-secondary);
  padding: 6px 12px;
  border-radius: 20px;
}

.baby-mode > .audio-level-bar {
  margin: 0 16px;
}

.baby-preview {
  flex: 1;
  width: 100%;
  object-fit: cover;
  background: #000;
}

.baby-error {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 24px;
  text-align: center;
  color: var(--danger);
  font-size: 16px;
}

.baby-stop {
  padding: 16px;
  margin: 16px;
  font-size: 16px;
  font-weight: 600;
  background: var(--bg-tertiary);
  color: var(--text-secondary);
  border: none;
  border-radius: var(--radius);
  cursor: pointer;
}
