.parent-mode {
  position: relative;
  height: 100%;
  background: #000;
}

.parent-video {
  width: 100%;
  height: 100%;
  object-fit: contain;
  background: #000;
}

.parent-overlay {
  position: absolute;
  inset: 0;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  pointer-events: none;
}

.parent-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 12px 16px;
  background: linear-gradient(to bottom, rgba(0,0,0,0.6), transparent);
  pointer-events: auto;
}

.parent-pin {
  font-size: 14px;
  color: var(--text-secondary);
  background: rgba(26, 26, 26, 0.8);
  padding: 6px 12px;
  border-radius: 20px;
}

.parent-level-bar {
  padding: 0 16px;
  pointer-events: none;
}

.parent-controls {
  display: flex;
  gap: 12px;
  padding: 16px;
  background: linear-gradient(to top, rgba(0,0,0,0.6), transparent);
  pointer-events: auto;
}

.parent-retry {
  flex: 1;
  padding: 16px;
  font-size: 16px;
  font-weight: 600;
  background: var(--accent);
  color: #fff;
  border: none;
  border-radius: var(--radius);
  cursor: pointer;
}

.parent-tap-overlay {
  position: fixed;
  inset: 0;
  z-index: 50;
  display: flex;
  align-items: center;
  justify-content: center;
  background: rgba(0, 0, 0, 0.8);
  cursor: pointer;
}

.parent-tap-btn {
  padding: 24px 48px;
  font-size: 22px;
  font-weight: 600;
  background: var(--accent);
  color: #fff;
  border: none;
  border-radius: var(--radius);
  cursor: pointer;
}

.parent-stop {
  flex: 1;
  padding: 16px;
  font-size: 16px;
  font-weight: 600;
  background: var(--bg-tertiary);
  color: var(--text-secondary);
  border: none;
  border-radius: var(--radius);
  cursor: pointer;
}
