.pin-entry {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100%;
  padding: 24px;
  gap: 20px;
}

.pin-title {
  font-size: 28px;
  font-weight: 700;
  color: var(--text-primary);
}

.pin-subtitle {
  font-size: 14px;
  color: var(--text-secondary);
  text-align: center;
}

.pin-input {
  width: 200px;
  padding: 16px;
  font-size: 32px;
  text-align: center;
  letter-spacing: 8px;
  background: var(--bg-secondary);
  border: 2px solid var(--bg-tertiary);
  border-radius: var(--radius);
  color: var(--text-primary);
  outline: none;
  transition: border-color 0.2s;
}

.pin-input:focus {
  border-color: var(--accent);
}

.pin-buttons {
  display: flex;
  gap: 16px;
  width: 100%;
  max-width: 400px;
  margin-top: 12px;
}

.pin-btn {
  flex: 1;
  padding: 20px 16px;
  font-size: 20px;
  font-weight: 600;
  border: none;
  border-radius: var(--radius);
  cursor: pointer;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 6px;
  transition: opacity 0.2s, transform 0.1s;
  color: #fff;
}

.pin-btn:active {
  transform: scale(0.97);
}

.pin-btn:disabled {
  opacity: 0.3;
  cursor: not-allowed;
  transform: none;
}

.pin-btn-desc {
  font-size: 11px;
  font-weight: 400;
  opacity: 0.8;
}

.pin-btn-baby {
  background: var(--accent);
}

.pin-btn-parent {
  background: var(--success);
  color: #0a0a0a;
}
