.cry-alarm-overlay {
  position: fixed;
  inset: 0;
  z-index: 100;
  display: flex;
  align-items: center;
  justify-content: center;
  background: rgba(255, 40, 40, 0.85);
  animation: alarm-flash 1s ease-in-out infinite;
  cursor: pointer;
}

@keyframes alarm-flash {
  0%, 100% { background: rgba(255, 40, 40, 0.85); }
  50% { background: rgba(200, 0, 0, 0.95); }
}

.cry-alarm-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
}

.cry-alarm-icon {
  width: 80px;
  height: 80px;
  border-radius: 50%;
  background: rgba(255, 255, 255, 0.2);
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 48px;
  font-weight: 700;
  color: #fff;
  animation: alarm-pulse 0.6s ease-in-out infinite alternate;
}

@keyframes alarm-pulse {
  from { transform: scale(1); }
  to { transform: scale(1.15); }
}

.cry-alarm-title {
  font-size: 28px;
  font-weight: 700;
  color: #fff;
}

.cry-alarm-dismiss {
  padding: 16px 48px;
  font-size: 18px;
  font-weight: 600;
  background: rgba(255, 255, 255, 0.25);
  color: #fff;
  border: 2px solid rgba(255, 255, 255, 0.5);
  border-radius: var(--radius);
  cursor: pointer;
}
